.Loader_ldsDualDing__1exxv {
    display: inline-block;
    position: absolute;
    top: 45%;
    left:50%;
    transform:translate(-50%, -50%);
    width: 80px;
    height: 80px;
  }
.Loader_ldsDualDing__1exxv:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    animation: Loader_ldsDualDing__1exxv 1.2s linear infinite;
}
@keyframes Loader_ldsDualDing__1exxv {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}
